.hf-header{
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: #151515;
  z-index: 100;
}

.hf-user{
  float:right;
  margin-right: 20px;
  top: calc(50%);
  margin-top: 0px;
  transform: translateY(-50%);
  display: block;
  width: 80vw;
}

.hf-user * {
  float: right;
  margin-right: 5px;
}

.hf-user * * {
  float: right;
  margin-right: 0px;
}

.hf-left{
  position: absolute;
  left: 0;
  margin-left: 20px;
  height: 50%;
  float: left;
  width: 20vw;
  /*top: 50%;*/
  margin-top: 0px;
  transform: translateY(-50%);
}

.hf-left-children {
  float: left;
  margin-right: 5px;
}

.my-avatar:hover{
  cursor: pointer;
}

.hf-footer{
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: #151515;
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  line-height: 100px;
  z-index: 100;
}